import React, { useRef, useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { Checkbox, IconCookies, ReactModal, Text } from '../../components'
import CookieConsent from 'react-cookie-consent'
import Cookies from 'js-cookie'
import { usePage } from '@inertiajs/inertia-react'

const Cookie = ({ AcceptAllCookiesCallback, SaveSettingsCallback }) => {
  const { t } = useTranslation()
  const [analyticsCookie, setAnalyticsCookie] = useState(false)
  const [thirdPartyCookie, setThirdPartyCookie] = useState(false)

  const handleAllCookies = () => {
    setAnalyticsCookie(true)
    setThirdPartyCookie(true)
    AcceptAllCookiesCallback(true, true)
  }
  const handleChosenCookies = () => {
    setAnalyticsCookie(analyticsCookie)
    setThirdPartyCookie(thirdPartyCookie)
    SaveSettingsCallback(analyticsCookie, thirdPartyCookie)
  }

  return (
    <div className='form-container'>
      <div className='form-body'>
        <Checkbox
          key='functional_cookies'
          formField={{
            name: 'functional_cookies',
            label: t('cookies.functional_cookies'),
            readOnly: true
          }}
          value
          onChange={(e) => {
          }}
        />
        <Text
          key='functional_cookies_content'
          formField={{
            name: 'functional_cookies_content',
            disableParse: true,
            text: <p className='message'>{t('cookies.functional_cookies_content')}</p>
          }}
        />
        <Checkbox
          key='analytics_cookies'
          formField={{
            name: 'analytics_cookies',
            label: t('cookies.analytics_cookies'),
            readOnly: false
          }}
          value={analyticsCookie}
          onChange={(e) => {
            setAnalyticsCookie(!analyticsCookie)
          }}
        />
        <Text
          key='analytics_cookies_content'
          formField={{
            name: 'analytics_cookies_content',
            disableParse: true,
            text: <p className='message'>{t('cookies.analytics_cookies_content')}</p>
          }}
        />
        <Checkbox
          key='third_party_cookies'
          formField={{
            name: 'third_party_cookies',
            label: t('cookies.third_party_cookies'),
            readOnly: false
          }}
          value={thirdPartyCookie}
          onChange={(e) => {
            setThirdPartyCookie(!thirdPartyCookie)
          }}
        />
        <Text
          key='third_party_cookies_content'
          formField={{
            name: 'third_party_cookies_content',
            disableParse: true,
            text: <p className='message'>{t('cookies.third_party_cookies_content')}</p>
          }}
        />
        <div className='form-footer'>
          <button className='btn btn-primary-ghost-2 btn-public btn-sm' type='button' onClick={() => handleChosenCookies()}>
            {t('cookies.save_settings')}
          </button>
          <button className='btn btn-primary btn-public btn-sm' type='submit' onClick={() => handleAllCookies()}>
            {t('cookies.accept_all')}
          </button>
        </div>
      </div>
    </div>
  )
}

const CookiePolicy = () => {
  const { t } = useTranslation()
  const modalRef = useRef()
  const [visible, setVisible] = useState(Cookies.get('consent-cookie') ? 'hidden' : 'show ')
  const [timer, setTimer] = useState()
  const { links } = usePage().props

  const handleCookiePolicy = () => {
    const title = t('cookies.summary_title')
    const body = (
      <Cookie
        AcceptAllCookiesCallback={handleAcceptAllCookiesCallback}
        SaveSettingsCallback={handleSaveSettingsCallback}
      />
    )
    modalRef.current.setContent(title, body)
  }

  function setCookiesAndVisibility (analyticsCookie, thirdPartyCookie) {
    clearTimeout(timer)
    setVisible('hidden')
    Cookies.set('consent-cookie', true, { expires: 3650, sameSite: 'Lax' })
    Cookies.set('functional-cookies', true, { expires: 3650, sameSite: 'Lax' })
    Cookies.set('analytics-cookies', analyticsCookie ?? true, { expires: 3650, sameSite: 'Lax' })
    Cookies.set('third-party-cookies', thirdPartyCookie ?? true, { expires: 3650, sameSite: 'Lax' })
  }

  const handleAcceptAllCookiesCallback = (analyticsCookie, thirdPartyCookie) => {
    setCookiesAndVisibility(analyticsCookie, thirdPartyCookie)
    modalRef.current.close()
  }

  const handleSaveSettingsCallback = (analyticsCookie, thirdPartyCookie) => {
    setCookiesAndVisibility(analyticsCookie, thirdPartyCookie)
    modalRef.current.close()
  }
  useEffect(
    () => {
      if (!Cookies.get('consent-cookie')) {
        setTimer(setTimeout(() => {
          setCookiesAndVisibility(false, false)
        }, 900000))
      }
    }, []
  )
  return (
    <>
      <CookieConsent
        location='bottom'
        visible={visible}
        cookieName='consent-cookie'
        style={{ background: '#E2F0F3' }}
        buttonStyle={{ display: 'none' }}
      >
        <div className='cookies-banner'>
          <div className='container'>
            <div className='cookies-banner__wrapper'>
              <div className='cookies-banner__info'>
                <IconCookies />
                <div className='ml-xl'>
                  <h4>{t('cookies.cookie_title')}</h4>
                  <p>{t('cookies.content')}
                    <a href={links.privacyPolicy.index} target='_blank' rel='noreferrer'>{t('cookies.privacy_policy')}</a>.
                  </p>
                </div>
              </div>

              <button
                type='button' className='btn btn-primary-ghost-2 btn-public btn-sm'
                onClick={() => handleCookiePolicy()}
              >
                {t('cookies.manage_settings')}
              </button>
              <button
                type='button' className='btn btn-primary btn-public btn-sm'
                onClick={() => setCookiesAndVisibility()}
              >
                {t('cookies.accept_cookies')}
              </button>
            </div>
          </div>
        </div>
        <ReactModal
          ref={modalRef} className='ReactModal__Content--Medium ReactModal__Cookies public-page'
          overlayClassName='ReactModal__Overlay--dark'
        />
      </CookieConsent>
    </>
  )
}
export default CookiePolicy
