import React, { useEffect, useState } from 'react'
import { usePage } from '@inertiajs/inertia-react'
import { api } from '../../../../../utils'
import { ethers } from 'ethers'
import { useTranslation } from 'react-i18next'
import { Inertia } from '@inertiajs/inertia'
import parse from 'html-react-parser'
import { CopyToClipboard } from 'react-copy-to-clipboard/src'
import { useToasts } from 'react-toast-notifications'
import OpenApp from 'react-open-app'
import { isMobile, isTablet } from 'react-device-detect'

const Metamask = ({ modalRef, setIsModalOpen }) => {
  const { auth, links, app, url } = usePage().props
  const [wallet, setWallet] = useState(auth ? auth.user.wallet : null)
  const [balance, setBalance] = useState(0)
  const [metamaskConnectionOpen, setMetamaskConnectionOpen] = useState(false)
  const { t } = useTranslation()
  const { addToast } = useToasts()
  const isMobileTablet = isMobile || isTablet
  const metaMaskUrl = `https://metamask.app.link/dapp/${url.current_full.replace('http://', '').replace('https://', '')}`
  // const modalRef = useRef()

  useEffect(() => {
    if (wallet) {
      getBalance(wallet)
    }
  }, [wallet])

  useEffect(() => {
    if (auth) {
      setWallet(auth.user.wallet)
    }
  }, [auth])

  const handleConnect = () => {
    if (!auth) {
      const title = ''
      const body = (
        <p>
          {parse(t('metamask.need_auth', { link: `<a href="${links.user.login}" class="text-link">${t('ui.sign_in')}</a>` }))}
        </p>
      )
      modalRef.current.setContent(title, body)
    } else {
      if (window.ethereum) {
        setMetamaskConnectionOpen(true)
        window.ethereum
          .request({ method: 'eth_requestAccounts' })
          .then((res) => {
            const wallet = res[0]
            api.post(links.user.setWallet, {
              wallet: wallet
            })
              .then((res2) => {
                setMetamaskConnectionOpen(false)
                Inertia.reload()
              })
              .catch((error) => {
                setMetamaskConnectionOpen(false)
                if (error.response.status === 422) {
                  const title = ''
                  const body = (
                    <p>
                      {t('metamask.used_wallet')}
                    </p>
                  )
                  modalRef.current.setContent(title, body)
                }
              })
          })
          .catch((error) => {
            if (error) {
              setMetamaskConnectionOpen(false)
              const title = ''
              const body = (
                <p>
                  {t('metamask.something_went_wrong_with_connect')}
                </p>
              )
              modalRef.current.setContent(title, body)
            }
          })
      } else {
        const title = ''
        const body = (
          <p>
            {parse(t('metamask.no_metamask', {
              link: `<a href="https://youtu.be/PwmWbJl-xNA" target='_blank' class='text-link'>${t('ui.video').toLowerCase()}</a>`,
              link2: `<a href="/assets/pdf/wawstreet_how_to_metamask_${app.locale}.pdf" target="_blank" rel="noreferrer"  class='text-link'>${t('ui.pdf_file')}</a>`
            }))}
          </p>
        )
        modalRef.current.setContent(title, body)
      }
    }
  }

  const handleDisconnect = () => {
    setIsModalOpen(true)
    const title = ''
    const body = (
      <>
        <p>{t('metamask.disconnect_message')}</p>

        <div className='form-footer form-footer-right col-md-12'>

          <button
            className='btn btn-primary-ghost'
            type='submit'
            onClick={() => {
              modalRef.current.close()
              setIsModalOpen(false)
            }}
          >
            {t('ui.no')}
          </button>

          <button
            className='btn btn-primary'
            type='submit'
            onClick={() => {
              api.post(links.user.setWallet, {
                wallet: null
              })
                .then((res2) => {
                  modalRef.current.close()
                  Inertia.reload()
                  setIsModalOpen(false)
                })
            }}
          >
            {t('ui.yes')}
          </button>
        </div>
      </>
    )
    modalRef.current.setContent(title, body)
  }

  const getBalance = (address) => {
    // Requesting balance method
    if (window.ethereum) {
      window.ethereum
        .request({
          method: 'eth_getBalance',
          params: [address, 'latest']
        })
        .then((balance) => {
          // Setting balance
          setBalance(ethers.utils.formatEther(balance))
        })
    }
  }

  const copyToClipboard = () => {
    addToast(t('metamask.copied_to_clipboard'), { appearance: 'success' })
  }

  return (
    <>
      <div className='dropdown__menu dropdown__wallet'>
        <div className='dropdown__header'>
          <h4 className='dropdown__title'>{t('metamask.my_wallet')}</h4>
          {auth && wallet && window.ethereum &&
            <CopyToClipboard text={wallet} onCopy={() => copyToClipboard()}>
              <a className='dropdown__link'>
                {wallet.substring(0, 4)}
                ...
                {wallet.substring(wallet.length - 4, wallet.length)}
              </a>
            </CopyToClipboard>}
        </div>
        <div className='dropdown__body'>
          {auth && wallet && window.ethereum &&
            <>
              <div className='mock__input'>
                <p>{t('metamask.current_balance')}</p>
                <h5>{parseFloat(balance).toFixed(4)} MATIC</h5>
              </div>
              <button
                className='btn btn-primary-ghost'
                onClick={handleDisconnect}
              >{t('metamask.disconnect_wallet')}
              </button>
            </>}
          {(!auth || (auth && !wallet) || !window.ethereum) &&
            <>
              <p className='text-14'>
                {parse(t('metamask.description', {
                  link: `<a className='text-underline text-primary text-bold' 
                href=${links.termsAndConditions.index} target="_blank">${t('metamask.terms_of_service')}</a>`
                }))}
              </p>

              {(!isMobileTablet || (isMobileTablet && window.ethereum)) &&
                <button
                  className='btn btn-primary-ghost'
                  onClick={handleConnect}
                >{metamaskConnectionOpen ? t('metamask.connecting_to_metamask') : t('metamask.connect_to_metamask')}
                </button>}

              {(isMobileTablet && !window.ethereum) &&
                <OpenApp
                  href={metaMaskUrl}
                  android={metaMaskUrl}
                  ios={metaMaskUrl}
                >
                  <span className='btn btn-primary-ghost width-100'>
                    {metamaskConnectionOpen ? t('metamask.connecting_to_metamask') : t('metamask.connect_to_metamask')}
                  </span>
                </OpenApp>}

              <div className='dropdown__section'>
                <h4 className='text-dark-blue-2'>{t('metamask.need_help')}</h4>
                <p className='text-14'>{t('metamask.we_give_answer')}</p>
              </div>
              <div className='dropdown__section'>
                <a className='dropdown__link' href='https://youtu.be/PwmWbJl-xNA' target='_blank' rel='noreferrer'>
                  {t('metamask.how_to_create')}
                </a>
                <a href={'/assets/pdf/wawstreet_how_to_metamask_' + app.locale + '.pdf'} className='dropdown__link' target='_blank' rel='noreferrer'>
                  {t('ui.download_pdf')}
                </a>
                <a className='dropdown__link' href='https://youtu.be/MGHCff9rjGk' target='_blank' rel='noreferrer'>
                  {t('metamask.how_to_connect')}
                </a>
                <a href={'/assets/pdf/wawstreet_how_to_metamask_' + app.locale + '.pdf'} className='dropdown__link' target='_blank' rel='noreferrer'>
                  {t('ui.download_pdf')}
                </a>
              </div>
            </>}
        </div>
      </div>
    </>
  )
}

export default Metamask
