import React, { useState } from 'react'
import { usePage } from '@inertiajs/inertia-react'
import { IconTrash } from '../../../../Icons'
import { api, trans } from '../../../../../utils'
import { FieldError } from '../../../../Form/FormElements/FieldError'
import { EventEmitter } from '../../../../../utils/eventEmitter'

const BasketItem = ({ saleId, order, index, setBasketData, checkoutErrors }) => {
  const { links } = usePage().props

  const [quantityError, setQuantityError] = useState([])

  const handleBasketQuantityChange = (type, saleId) => {
    setQuantityError([])
    const data = {
      saleId: saleId,
      type: type
    }

    api.post(
      links.order.basketQuantityChange,
      data
    )
      .then((res) => {
        const result = Object.keys(res.data.basket).map((key) => [Number(key), res.data.basket[key]])
        setBasketData(result)
        EventEmitter.emit('update-basket-counter', res.data.basketCounter)
      }).catch((error) => {
        setQuantityError(error.response.data.errors.saleId)
      })
  }

  const handleDeleteFromBasket = (saleId) => {
    const data = {
      saleId: saleId
    }

    api.post(
      links.order.deleteFromBasket,
      data
    )
      .then((res) => {
        const result = Object.keys(res.data.basket).map((key) => [Number(key), res.data.basket[key]])
        setBasketData(result)
        EventEmitter.emit('update-basket-counter', res.data.basketCounter)
        setQuantityError([])
      })
  }

  return (
    <div className='basket__nft basket__section' key={index}>
      <div className='basket__nft-thumbnail'>
        <img src={order.nftImage} alt='nft thumbnail' />
        <div className='basket__nft-price'>{` ${parseFloat(order.price)}`}</div>
      </div>
      <div className='basket__nft-details'>
        <h4 className='basket__nft-name'>{order.nftName}</h4>
        <p className='basket__nft-collection-name'><small>{trans(order.collectionName)}</small></p>
        <div className='basket__nft-order'>
          <div className='basket__nft-quantity'>
            <button
              type='button'
              className='btn-stripped btn-circle'
              onClick={() => handleBasketQuantityChange('minus', saleId)}
            >
              -
            </button>
            <span className='basket__nft-quantity-indicator'>{order.quantity}</span>
            <button
              type='button'
              className='btn-stripped btn-circle'
              onClick={() => handleBasketQuantityChange('plus', saleId)}
            >
              +
            </button>
            <FieldError errors={quantityError.length ? quantityError : checkoutErrors} />
          </div>
          <div className='basket__nft-remove'>
            <button className='btn-stripped btn-circle btn-circle--dark' onClick={() => handleDeleteFromBasket(saleId)}>
              <IconTrash />
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default BasketItem
