import { useState } from 'react'
import Slider from 'react-slick'
import { Accordion } from '../../../Accordion/Accordion'
import faqBackground from '../../../../../assets/images/public/image-background-header.jpg'
import { IconChevronDown, IconChevronUp } from '../../../../components/Icons'
import { useTranslation } from 'react-i18next'
import parse from 'html-react-parser'
import { usePage } from '@inertiajs/inertia-react'

const FaqPublic = () => {
  const { t } = useTranslation()
  const { links, app } = usePage().props
  const [activeQuestion, setActiveQuestion] = useState(null)

  const setIsOpenQuestion = (activeQuestionLocal) => {
    setActiveQuestion(activeQuestion === activeQuestionLocal ? null : activeQuestionLocal)
    // setActiveProject(null)
  }

  const settings = {
    className: 'slick-overflow-right',
    arrows: false,
    dots: true,
    infinite: true,
    centerMode: true,
    centerPadding: '18%',
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 576
      },
      {
        breakpoint: 10000,
        settings: 'unslick'
      }
    ]
  }

  const faqList = [
    {
      id: 1,
      faq_title: t('footer.faq.questions.what_is_nft'),
      faq_body: t('footer.faq.answers.what_is_nft')
    },
    {
      id: 2,
      faq_title: t('footer.faq.questions.how_to_by'),
      faq_body: parse(t('footer.faq.answers.how_to_by', {
        link: `<a href="https://youtu.be/PwmWbJl-xNA" target='_blank' class='text-white text-bold'>${t('ui.this_video').toLowerCase()}</a>`,
        link2: `<a href="/assets/pdf/wawstreet_how_to_metamask_${app.locale}.pdf" target="_blank" rel="noreferrer"  class='text-white text-bold'>${t('ui.pdf_file_2')}</a>`
      }))
    },
    {
      id: 3,
      faq_title: t('footer.faq.questions.creator'),
      faq_body: parse(t('footer.faq.answers.creator', { linkContact: links.contactUs.index }))
    },
    {
      id: 4,
      faq_title: t('footer.faq.questions.sell_nft'),
      faq_body: t('footer.faq.answers.sell_nft')
    },
    {
      id: 5,
      faq_title: t('footer.faq.questions.contact'),
      faq_body: parse(t('footer.faq.answers.contact', { linkFaq: links.faq.index, linkContact: links.contactUs.index }))
    }
  ]

  return (
    <section className='faq faq--shared'>
      <div className='container'>
        <div className='row'>
          <div className='col-12 col-lg-6'>
            <div className='faq__split faq__split--left'>
              <div className='faq__split-bg'>
                <img src={faqBackground} alt='background for faq section' />
              </div>
              <h3 className='faq__heading'><span className='text-light-green-2'>{t('faq.quickLinks_title.start')}</span>{t('faq.quickLinks_title.your_journey_in_the_nfts_world')}</h3>
              <div className='faq__split-tags'>
                <Slider {...settings}>
                  <a href={links.faq.index} className='tags__item'>{t('homepage.quickLinks.faq')}</a>
                  <a href={links.collection.public.index} className='tags__item'>{t('homepage.quickLinks.list_collections')}</a>
                  <a href={links.creators.public.index} className='tags__item'>{t('homepage.quickLinks.list_creators')}</a>
                  <a href={links.user.register.index} className='tags__item'>{t('homepage.quickLinks.sign_up_page')}</a>
                  <a href={links.aboutUs.index} className='tags__item'>{t('homepage.quickLinks.about_us')}</a>
                </Slider>
              </div>
            </div>
          </div>
          <div className='col-12 col-lg-6'>
            <div className='faq__split faq__split--right'>
              <div className='faq__split-bg' />
              <h3 className='faq__heading'>{t('faq.quickLinks_title.we_answer')}<span className='text-light-green-2'>{t('faq.quickLinks_title.your_questions')}</span></h3>
              <div className='faq__questions'>
                {faqList.map(faqItem => {
                  return (
                    <Accordion
                      key={'team-' + faqItem.id}
                      isOpen={activeQuestion === faqItem.id}
                      onToggle={() => setIsOpenQuestion(faqItem.id)}
                      customWrapperClass='accordion__stacks accordion--xl'
                      heading={
                        <>
                          <span>{faqItem.faq_title}</span>
                          {activeQuestion === faqItem.id ? <IconChevronUp className='fill-light-green-2' /> : <IconChevronDown className='fill-light-green-2' />}
                        </>
                    }
                    >
                      <div className='accordion__body'>
                        <p className='text-white'>{faqItem.faq_body}</p>
                      </div>
                    </Accordion>
                  )
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default FaqPublic
