import React, { useEffect, useState } from 'react'
import { usePage } from '@inertiajs/inertia-react'
import { api } from '../../../../../utils'
import BasketItem from './BasketItem'
import { useTranslation } from 'react-i18next'
import { Inertia } from '@inertiajs/inertia'
import { FieldError } from '../../../../Form/FormElements/FieldError'
import { EventEmitter } from '../../../../../utils/eventEmitter'
import { IconBasketEmpty } from '../../../../Icons'
import parse from 'html-react-parser'

const Basket = () => {
  const { links, auth } = usePage().props
  const { t } = useTranslation()
  const [basketData, setBasketData] = useState([])
  const [basketMessage, setBasketMessage] = useState(null)
  const [paymentResponseUrl, setPaymentResponseUrl] = useState(null)
  const [errors, setErrors] = useState([])
  const [redirectingCheckout, setRedirectingCheckout] = useState(false)

  useEffect(() => {
    if (paymentResponseUrl) {
      window.location = paymentResponseUrl
    }
  }, [paymentResponseUrl])

  useEffect(() => {
    api.get(
      links.order.getBasketData
    )
      .then((res) => {
        const result = Object.keys(res.data.basket).map((key) => [Number(key), res.data.basket[key]])
        setBasketData(result)
        setBasketMessage(parse(`<p className='text-center'>${t('order.basket.empty_basket')}</p>`))
      })
  }, [])

  const calculateTotalCost = () => {
    let total = 0
    basketData.forEach((element, index) => {
      total = total + (element[1].price * element[1].quantity)
    })
    return total
  }

  const handleCheckoutBasket = (e) => {
    e.target.disabled = true

    const data = {
      basket: true
    }

    if (!auth) {
      Inertia.visit(links.user.login)
    } else if (!auth.user.wallet) {
      document.getElementById('meta-mask-button').click()
    } else {
      setRedirectingCheckout(true)
      api.post(
        links.order.checkoutBasket,
        data
      )
        .then((res) => {
          setPaymentResponseUrl(res.data.redirectUrl)
        })
        .catch((error) => {
          setRedirectingCheckout(false)
          e.target.disabled = false
          setErrors(JSON.parse(error.response.data.errors.basket[0]))
        })
    }
  }

  EventEmitter.on('clear-basket', () => {
    setBasketData([])
  })

  return (
    <>
      <div className='dropdown__menu dropdown__basket'>
        <div className='dropdown__header'>
          <h4 className='dropdown__title'>{t('order.basket.title')}</h4>
        </div>
        <div className='dropdown__body basket'>
          <div className='dropdown__max-height dropdown__max-height--30vh'>
            {!!basketData.length && basketData.map((element, index) => {
              const order = element[1]
              const saleId = element[0]
              const checkoutErrors = errors[saleId] ?? null
              return (
                <BasketItem
                  key={index}
                  saleId={saleId}
                  order={order}
                  index={index}
                  setBasketData={setBasketData}
                  checkoutErrors={checkoutErrors}
                />
              )
            })}
          </div>
          {!!basketData.length &&
            <>
              <div className='basket__orders basket__section'>
                <h3 className='basket__subtitle'>{t('order.basket.basket_info')}</h3>
                <ul className='basket__orders-list list'>
                  {basketData.map((element, index) => {
                    const order = element[1]

                    return (
                      <li key={index}>
                        <p className='basket__orders-label'>
                          <span className='basket__orders-quantity'>{`${order.quantity} x `}</span>
                          <span>{order.nftName}</span>
                        </p>
                        <p className='basket__orders-price'>{` ${parseFloat(order.price)}`}</p>
                      </li>
                    )
                  })}
                </ul>
              </div>

              <div className='basket__total'>
                <div className='flex-space-between'>
                  <h3 className='basket__subtitle'>{t('order.basket.total')}</h3>
                  <h3 className='basket__subtitle text-light-green-1'>{`${calculateTotalCost()} `}</h3>
                </div>
                {!redirectingCheckout &&
                  <div className='text-center'>
                    <button
                      type='button'
                      className='btn btn-primary'
                      onClick={(e) => handleCheckoutBasket(e)}
                    >
                      {t('ui.checkout')}
                    </button>
                  </div>}
                {redirectingCheckout &&
                  <div className='text-center'>
                    <p>{t('order.redirecting')}</p>
                  </div>}
                <div className='text-center'>
                  <FieldError errors={errors[0] ?? ''} />
                </div>
              </div>
            </>}
          {!basketData.length &&
            <div className='dropdown__basket--empty'>
              <IconBasketEmpty />
              {basketMessage}
            </div>}
        </div>
      </div>
    </>
  )
}

export default Basket
