import ReactPopover from '../../../Popover/ReactPopover'
import { IconArrowThin } from '../../../../components/Icons'
import { usePage } from '@inertiajs/inertia-react'
import { route } from '../../../../utils'

const LanguageDropdown = ({ position }) => {
  const { app, languages, links } = usePage().props
  return (
    <ReactPopover
      content={
        <div className='dropdown__menu'>
          <ul className='list'>
            {
              languages.map((language, i) =>
                <li key={`language-${i}`}>
                  <a href={route(links.setLocale, { id: language.value })}>{language.locale.toUpperCase()}</a>
                </li>
              )
            }
          </ul>
        </div>
      }
      position={position}
    >
      <button className='btn btn-primary-ghost btn-language btn-icon-right btn-public'>{app.locale}<IconArrowThin className='icon-270 fill-white' /></button>
    </ReactPopover>
  )
}

export default LanguageDropdown
