import React, { useEffect, useRef, useState } from 'react'
import { usePage } from '@inertiajs/inertia-react'
import { api } from '../../utils'
import { Inertia } from '@inertiajs/inertia'
import { useTranslation } from 'react-i18next'

const MetamaskCheck = () => {
  const { auth, links } = usePage().props
  const [checkWallet, setCheckWallet] = useState(false)
  const modalRef = useRef()
  const { t } = useTranslation()

  useEffect(() => {
    if (auth && auth.user.wallet && window.ethereum) {
      window.ethereum.request({ method: 'eth_accounts' })
        .then((result) => {
          if ((result.length && !result.includes(auth.user.wallet)) || !result.length) {
            setCheckWallet(true)
          }
        })
        .catch(() => {
          const title = ''
          const body = (
            <p>
              {t('metamask.something_went_wrong_with_connect')}
            </p>
          )
          modalRef.current.setContent(title, body)
        })
    }
  }, [auth])

  useEffect(() => {
    if (checkWallet) {
      api.post(links.user.setWallet, {
        wallet: null
      })
        .then((res2) => {
          Inertia.reload()
        })
    }
  }, [checkWallet])

  useEffect(() => {
    if (auth) {
      if (!window.ethereum) {
        // Nothing to do here... no ethereum provider found
        return
      }
      const accountWasChanged = (accounts) => {
        if (!accounts.length || !accounts.includes(auth.user.wallet)) {
          api.post(links.user.setWallet, {
            wallet: null
          })
            .then((res2) => {
              Inertia.reload()
            })
        }
      }

      window.ethereum.on('accountsChanged', accountWasChanged)
    }
  }, [])

  return (
    <></>
  )
}

export default MetamaskCheck
