import React, { useState, useEffect, useRef } from 'react'
import Slider from 'react-slick'
import {
  IconMenuPublic,
  IconMenuPublicClosed,
  IconWallet,
  IconCart,
  IconChevronRight,
  IconArrowThin,
  IconUser, IconCube2, IconChevronLeft, IconShare, IconChevronDown, IconChevronUp
} from '../../../../components/Icons'
import headerBackground from '../../../../../assets/images/public/image-background-header.jpg'
import WawStreetLogo from './WawStreetLogo'
import ReactPopover from '../../../Popover/ReactPopover'
import LanguageDropdown from './LanguageDropdown'
import { usePage } from '@inertiajs/inertia-react'
import { useTranslation } from 'react-i18next'
import { route, Link, trans } from '../../../../utils'
import { SOCIAL_MEDIAS_ICONS } from '../../../../domain/Shared/Constants'
import Metamask from './Header/Metamask'
import { ReactModal } from '../../../index'
import Basket from './Header/Basket'
import { EventEmitter } from '../../../../utils/eventEmitter'
import IconInfo from '../../../Icons/IconInfo'
import ReactTooltip from '../../../Tooltip/ReactTooltip'
import { Inertia } from '@inertiajs/inertia'

const HeaderPublic = ({ headerBg, headerItemDetails }) => {
  const { auth, links, activeCategories, basketCount, activeRouteKey } = usePage().props
  const [showDetails, setShowDetails] = useState(!!(headerItemDetails && headerItemDetails.alwaysOpen))
  const [isPageScrolled, setIsPageScrolled] = useState(false)
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const { t } = useTranslation()
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [basketCounter, setBasketCounter] = useState(basketCount)
  const modalRef = useRef()

  const toggleDetails = () => {
    setShowDetails(!showDetails)
  }

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY

      if (scrollTop > 50 && !isPageScrolled) {
        setIsPageScrolled(true)
      } else if (scrollTop <= 50 && isPageScrolled) {
        setIsPageScrolled(false)
      }
    }

    window.addEventListener('scroll', handleScroll)

    return () => {
      window.removeEventListener('scroll', handleScroll)
    }
  }, [isPageScrolled])

  EventEmitter.on('update-basket-counter', (basketCounter) => {
    setBasketCounter(basketCounter)
  })

  const settings = ({
    className: 'slick-centered',
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    arrows: false,
    dots: true,
    draggable: true,
    centerMode: true,
    centerPadding: '30px',
    responsive: [
      {
        breakpoint: 10000,
        settings: 'unslick'
      },
      {
        breakpoint: 1220,
        settings: {
          slidesToShow: 3
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          centerPadding: '10%'
        }
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          centerPadding: '18%'
        }
      }
    ]
  })

  const socialMediaLinks = headerItemDetails?.socialMediaLinks && headerItemDetails.socialMediaLinks.map(sm => {
    const key = Object.keys(sm)[0]
    if (sm[key]) {
      return (
        <a
          key={key}
          rel='noreferrer'
          target='_blank'
          href={sm[key]}
          className='btn btn-sm btn-white btn-icon-center fill-dark-blue-2 mh-xxs mv-xxs text-14'
        >
          {SOCIAL_MEDIAS_ICONS[key]}
        </a>
      )
    }

    return ''
  })

  const handleMenuOpen = () => {
    setIsMenuOpen(prev => {
      document.getElementById('body').classList.toggle('body__menu--opened', !prev)
      return !prev
    })
  }

  const handleOpenCategoryDetail = (categoryId) => {
    window.open(route(`${links.collection.public.index}?categoryId=${categoryId}`), '_blank')
  }

  const handleSignIn = link => {
    handleMenuOpen()
    Inertia.visit(route(link))
  }

  return (
    <header className={`main-header header--sticky ${isPageScrolled ? 'header--scrolled' : ''}`}>
      <div className={`header__top ${isMenuOpen ? 'header__menu--opened' : ''}`}>
        {headerBg !== 'NO_IMAGE' &&
          <div className='main-header__bg'>
            <img src={headerBg ?? headerBackground} alt='Header background image' />
            <div className='main-header__bg-overlay' />
          </div>}
        <div className='container'>
          <div className='header__top header__top--mobile'>
            <WawStreetLogo />

            <div className='anchor-wrapper'>
              <nav className='header-nav'>
                <ul className='inline-list anchor-wrapper'>
                  <li className='dropdown'>
                    <ReactPopover
                      content={
                        <div className='dropdown__menu dropdown__collections'>
                          <div className='dropdown__body'>
                            <ul className='list dropdown__max-height'>
                              {
                                activeCategories.map((category, i) =>
                                  <li key={`category-${i}`}>
                                    <Link href={`${links.collection.public.index}?categoryId=${category.id}`}> {trans(category.name)} </Link>
                                  </li>
                                )
                              }
                            </ul>
                            <a className='dropdown__standalone-link' href={links.collection.public.index}>{t('header.view_all_collections')}<IconChevronRight /></a>
                          </div>
                        </div>
                      }
                    >
                      <span className='dropdown__btn'>{t('header.explore_collections')}</span>
                    </ReactPopover>
                  </li>
                  <li><a href={links.creators.public.index}>{t('header.creators')}</a></li>
                  <li><a href={links.aboutUs.index}>{t('header.about_us')}</a></li>
                </ul>
              </nav>

              <div className='anchor-wrapper header-links'>
                {!auth &&
                  <>
                    <button className='btn btn-primary-ghost btn-public btn-user-desk'><Link href={links.user.login}>{t('header.sign_in')}</Link></button>
                    <button className='btn btn-primary btn-public btn-user-desk'><Link href={links.user.register.index}>{t('header.sign_up')}</Link></button>
                  </>}
                {!auth &&

                  <ReactPopover
                    content={
                      <div className='dropdown__menu'>
                        <div className='dropdown__header'>
                          <h4 className='dropdown__title'>Connection</h4>
                        </div>
                        <div className='dropdown__body'>
                          <button className='btn btn-primary-ghost btn-public mt-0 mb-xs'>Connectez-vous</button>
                          <button className='btn btn-primary btn-public mv-0'>Inscrivez-vous</button>
                        </div>
                      </div>
                    }
                  >
                    <button className='btn btn-primary-ghost btn-icon-center btn-public btn-user-tablet'>
                      <IconUser className='fill-white' />
                    </button>
                  </ReactPopover>}

                {auth &&
                  <ReactPopover
                    content={
                      <div className='dropdown__menu'>
                        <div className='dropdown__body'>
                          <ul className='list'>
                            <li><a href={links.user.myProfile.index}>{t('header.manage_my_profile')}</a></li>
                            <li><a href={links.collection.user.myCollectionsGrid}>{t('header.my_collections')}</a></li>
                            <li><a href={links.transaction.public.myTransactionsIndex}>{t('header.my_transactions')}</a></li>
                            <li><a href={links.user.logout}>{t('header.sign_out')}</a></li>
                          </ul>
                        </div>
                      </div>
                    }
                  >
                    <button className='btn btn-primary-ghost btn-icon-right btn-public'>{t('header.my_account')}
                      <IconArrowThin
                        className='icon-270 fill-white'
                      />
                    </button>
                  </ReactPopover>}
                <ReactPopover
                  closeOnClickOutside={!isModalOpen}
                  content={<Metamask modalRef={modalRef} setIsModalOpen={setIsModalOpen} />}
                >
                  <button className='btn btn-primary-ghost btn-icon-center' id='meta-mask-button'><IconWallet className='fill-white' /></button>
                </ReactPopover>
                <ReactPopover
                  content={<Basket />}
                  closeOnClickOutside={!isModalOpen}
                >
                  <button className='btn btn-primary-ghost btn-icon-center' id='basket-button'>
                    <span className='icon-counter-wrapper-layer icon-counter-wrapper'>
                      <IconCart className='fill-white' />
                      {!!basketCounter && <span className='icon-counter'>{basketCounter}</span>}
                    </span>
                  </button>
                </ReactPopover>
                <LanguageDropdown />
              </div>

              <button className={`menu__burger ${isMenuOpen ? 'menu__burger--opened' : ''}`} onClick={handleMenuOpen}>
                {isMenuOpen
                  ? <IconMenuPublicClosed />
                  : <IconMenuPublic />}
              </button>
            </div>
          </div>
          {headerItemDetails &&
            <div className='header__item-preview'>
              {headerItemDetails && headerItemDetails.myCollectionDetails &&
                <div className='header__nav-links'>
                  <Link href={headerItemDetails.myCollectionDetails.backToCollections} className='btn-back text-16 text-white'>
                    <IconChevronLeft />
                    {t('collections.public.user.back_to_my_collections')}
                  </Link>
                  <Link href={headerItemDetails.myCollectionDetails.detailViewOfCollection} className='btn-back text-16 text-white btn-link-warning text-right'>
                    {t('collections.public.user.go_to_collection')}
                  </Link>
                </div>}
              <h2 className='text-white public-h2'>{headerItemDetails.title}</h2>
              {headerItemDetails.author &&
                <h4 className='public-h4'>{t('header.created_by')} <span><a href={route(links.creators.public.showCollectionsGrid, { creator: headerItemDetails.author.id })}>{headerItemDetails.author.name}</a> </span></h4>}
              <div className='header__item-thumbnail'>
                <img src={headerItemDetails.thumbnail} alt='Item details thumbnail' />
              </div>
            </div>}
        </div>
      </div>

      <div className={`menu__primary ${isMenuOpen ? 'menu__primary--opened' : ''}`}>
        <div className='container'>
          <div className='header__top header__top--mobile'>
            <WawStreetLogo />

            <div className='anchor-wrapper header-links'>
              <ReactPopover
                content={<Basket />}
                closeOnClickOutside={!isModalOpen}
              >
                <button className='btn btn-primary-ghost btn-icon-center' id='basket-button-mobile'>
                  <span className='icon-counter-wrapper-layer icon-counter-wrapper'>
                    <IconCart className='fill-white' />
                    {!!basketCounter && <span className='icon-counter'>{basketCounter}</span>}
                  </span>
                </button>
              </ReactPopover>
              <LanguageDropdown />
            </div>

            <button className='menu__burger' onClick={handleMenuOpen}>
              <IconMenuPublicClosed />
            </button>
          </div>
        </div>
        <nav className='menu__nav'>
          <div className='container'>
            <ul className='menu__list'>
              <li className='menu__item'>
                <div className='flex-align-center justify-between'>
                  <h3 className='menu__heading'>Collections</h3>
                  <a href={links.collection.public.index} className='menu__link-more'>{t('header.view_all_collections')} <IconChevronRight className='fill-current' /></a>
                </div>
                <div className='menu__slider'>
                  <Slider {...settings}>
                    {activeCategories.map((cat) => {
                      return (
                        <div key={cat.id} className='card card--md' onClick={() => handleOpenCategoryDetail(cat.id)}>
                          <div className='card__header card__section--no-top-padding'>
                            <div className='card__thumbnail'>
                              <div className='card__thumbnail--ratio-071'>
                                <img src={cat.image} alt='Card thumbnail' />
                              </div>
                            </div>
                          </div>
                          <div className='card__body text-center'>
                            <h5 className='public-h5 ellipsis__line'>{trans(cat.name)}</h5>
                          </div>
                        </div>
                      )
                    })}
                  </Slider>
                </div>
              </li>
              <li className='menu__item'>
                <h3 className='menu__heading'><a href={links.creators.public.index}>{t('header.creators')}</a></h3>
              </li>
              <li className='menu__item'>
                <h3 className='menu__heading'><a href='#'>{t('header.about_us')}</a></h3>
              </li>
            </ul>
          </div>
        </nav>
        <div className='menu__auth'>
          <div className='container'>
            <div className='menu__auth-wrapper'>
              {auth &&
                <>
                  <div className='menu__auth-item'>
                    <ReactPopover
                      position={['top']}
                      content={
                        <div className='dropdown__menu'>
                          <div className='dropdown__body'>
                            <ul className='list'>
                              <li><a href={links.user.myProfile.index}>{t('header.manage_my_profile')}</a></li>
                              <li><a href={links.collection.user.myCollectionsGrid}>{t('header.my_collections')}</a></li>
                              <li><a href={links.transaction.public.myTransactionsIndex}>{t('header.my_transactions')}</a></li>
                              <li><a href={links.user.logout}>{t('header.sign_out')}</a></li>
                            </ul>
                          </div>
                        </div>
                      }
                    >
                      <button className='btn btn-primary-ghost btn-icon-right btn-public btn-block'>{t('header.my_account')}
                        <IconArrowThin
                          className='icon-270 fill-white'
                        />
                      </button>
                    </ReactPopover>
                  </div>
                  <div className='menu__auth-item menu__auth-wallet'>
                    <ReactPopover
                      position={['top']}
                      closeOnClickOutside={!isModalOpen}
                      content={<Metamask modalRef={modalRef} setIsModalOpen={setIsModalOpen} />}
                    >
                      <button className='btn btn-primary-ghost btn-icon-center'><IconWallet className='fill-white' /></button>
                    </ReactPopover>
                  </div>
                </>}
              {!auth &&
                <>
                  <div className='menu__auth-item'>
                    <button onClick={() => handleSignIn(links.user.login)} className='btn btn-primary-ghost btn-public btn-block'>{t('header.sign_in')}</button>
                  </div>
                  <div className='menu__auth-item'>
                    <button onClick={() => handleSignIn(links.user.register.index)} className='btn btn-primary btn-public btn-block'>{t('header.sign_up')}</button>
                  </div>
                </>}

              <div className='menu__auth-item menu__auth-lang'>
                <LanguageDropdown position={['top']} />
              </div>
            </div>
          </div>
        </div>
      </div>

      {headerItemDetails &&
        <div className={`header__details ${showDetails ? 'header__details--open' : ''} ${headerItemDetails.tabList && headerItemDetails.tabList.length ? 'header__details--with-tabs' : ''}`}>
          <div className='container'>
            <div className='header__details-links'>
              <div className='row'>
                <div className='col-3 col-sm-6'>
                  <div className='flex-align-center'>
                    {/* <a href='#' className='btn btn-sm btn-turquoise btn-icon-left btn-public'><IconPlus /> Ajouter a ma Collection</a> */}
                    {headerItemDetails.aboutAuthor &&
                      <>
                        <div className='header__details-links--desktop'>
                          <a
                            href={headerItemDetails.aboutAuthor} target='_blank'
                            className='btn btn-sm btn-white btn-public text-dark-blue-2' rel='noreferrer'
                          >
                            {t('header.about_the_author')}
                          </a>
                        </div>
                        <div className='header__details-links--mobile'>
                          <a
                            href={headerItemDetails.aboutAuthor} target='_blank'
                            className='btn btn-sm btn-turquoise btn-icon-center btn-public text-18' rel='noreferrer'
                          >
                            <IconUser />
                          </a>
                        </div>
                      </>}
                  </div>
                </div>
                <div className='col-9 col-sm-6 flex-align-center justify-end'>
                  {headerItemDetails.socialMediaLinks &&
                    <>
                      <div className='header__details-links--desktop flex-align-center'>
                        {socialMediaLinks}
                      </div>
                      <div className='header__details-links--mobile'>
                        {headerItemDetails.socialMediaLinks.some(item => Object.values(item)[0]?.length > 0) &&
                          <ReactPopover
                            content={
                              <div className='dropdown--md'>
                                <div className='dropdown__menu'>
                                  <div className='dropdown__body'>
                                    <div className='flex-align-center justify-between flex-wrap'>{socialMediaLinks}</div>
                                  </div>
                                </div>
                              </div>
                            }
                          >
                            <button className='btn btn-primary-ghost btn-icon-center mv-0'><IconShare className='fill-white' /></button>
                          </ReactPopover>}
                      </div>
                    </>}
                </div>
              </div>
            </div>
            {showDetails &&
              <div className='header__details-body'>
                <div className='row'>
                  <div className='col-12 col-lg-6'>
                    {headerItemDetails.figuresTitle &&
                      <h4 className='header__details-title'>{headerItemDetails.figuresTitle}</h4>}
                    <ul className='list-block-label'>
                      {headerItemDetails.figures && headerItemDetails.figures.map((fig, index) => {
                        return (
                          <li className='list-block-label__item' key={index + fig.figure + fig.label}>
                            <div className='list-block-label__casing'>
                              <div className='list-block-label__core'>
                                <h5 className='list-block-label__title'>{fig.figure}</h5>
                                <span className='list-block-label__subtitle'>{fig.label}</span>
                              </div>
                            </div>
                          </li>
                        )
                      })}
                    </ul>
                  </div>
                  <div className='col-12 col-lg-6 header__details-wrapper-mobile'>
                    <h4 className='header__details-title'>{headerItemDetails.detailsTitle}</h4>
                    <p className='text-white header__details-text'>{headerItemDetails.detailsInfo}</p>
                  </div>
                </div>
              </div>}
            {showDetails && headerItemDetails && headerItemDetails.myCollectionDetails &&
              <div className='header__details-body'>
                <div className='flex-space-between row'>
                  <div className='row'>
                    {headerItemDetails.myCollectionDetails &&
                      <h4 className='header__details-title'>{t('collections.public.user.header.collection_count', { value: headerItemDetails.myCollectionDetails.counter })}</h4>}
                  </div>
                  <div className='row text-right'>
                    <ReactTooltip content={t('collections.value_of_collection')}>
                      <IconInfo className='fill-white mr-xxs text-30' />
                    </ReactTooltip>
                    <h4 className='header__details-title'>{t('collections.public.user.header.collection_value', { value: headerItemDetails.myCollectionDetails.value })}</h4>
                  </div>
                </div>
                <div className='row justify-center'>
                  <div className='col-4 row text-center'>
                    {headerItemDetails.myCollectionDetails.scarcity.map((element, index) => {
                      return (
                        <div key={'scarcity-' + index} className='col-3'>
                          <IconCube2 key={index} className={element.class + ' text-18'} />
                          <span className='pl-xxs text-primary'><strong>{element.value}</strong></span>
                        </div>
                      )
                    })}
                  </div>
                </div>
              </div>}

            {!headerItemDetails.alwaysOpen &&
              <>
                {headerItemDetails.tabList && headerItemDetails.tabList.length &&
                  <div className='header__details-footer header__details-footer--tabs'>
                    <div className='tabs__block'>
                      {headerItemDetails.tabList.map((tab, index) => {
                        return (
                          <a
                            key={index}
                            className={`tabs__block-item ${activeRouteKey === tab.routeKey ? 'active' : ''}`}
                            href={tab.link ?? '#'}
                          >
                            {tab.label}
                          </a>
                        )
                      })}
                    </div>
                    {!headerItemDetails.alwaysOpen &&
                      <button
                        className='tabs__block-toggle' type='button'
                        onClick={toggleDetails}
                      >{showDetails ? <><IconChevronUp /> {t('ui.close')}</> : <><IconChevronDown /> {t('ui.open')}</>}
                      </button>}
                  </div>}

                {(!headerItemDetails.tabList || !headerItemDetails.tabList.length) &&
                  <div className='header__details-footer'>
                    {!headerItemDetails.alwaysOpen &&
                      <button
                        className='btn btn-dark btn-rounded' type='button'
                        onClick={toggleDetails}
                      >{showDetails ? t('ui.close') : t('ui.open')}
                      </button>}
                  </div>}
              </>}
          </div>
        </div>}
      <ReactModal ref={modalRef} />
    </header>
  )
}

export default HeaderPublic
